<template>
<div>
  <div class="about" :class="{'about-active': burgerActiveForNav}">
    <about-nav-bar></about-nav-bar>
    <header class="about-header">
      <img :src="clinics.image" alt="header-image">
    </header>
    <h3 style="color: #005C5F; padding: 30px 0; font-size: 40px">Врачи центра</h3>
    <vs-card-group>
      <vs-card v-for="(specialist, index) in specialists" @click="changeRouter(specialist.id)">
        <template #title>
          <h3>{{ specialist.full_name }}</h3>
        </template>
        <template #img>
          <img :src="specialist.photo" alt="">
        </template>
        <template #text>
          <p> {{ specialist.specialization }} </p>
        </template>
      </vs-card>
    </vs-card-group>
    <h3 style="color: #005C5F; padding: 30px 0; font-size: 40px">Услуги и цены</h3>
    <div class="services" style="padding-bottom: 100px">
      <el-collapse v-model="activeNames" style="background: white; margin-bottom: 20px">
        <el-collapse-item v-for="(service, index) in services" :title="service.title" :name="index" style="padding-left: 10px">
          <div>
            <div style="display:flex; justify-content: space-between; padding-right: 30px; margin-bottom: -15px;">
              <div class="checkbox">
                <vs-checkbox @change="buttonAble" color="#005C5F" :id="service.id" :name="service.service_type" :val="service.id" v-model="checked">{{ service.service_type }}</vs-checkbox>
              </div>
              <p style="font-size: 25px; font-weight: bold; color:#005C5F;">{{ service.price }} сум</p>
            </div>
            <el-button v-if="service.desc !== ''" style="margin-left: 30px" type="text" @click="open(service.desc)">Ещё...</el-button>
          </div>
        </el-collapse-item>
      </el-collapse>
      <form class="service-form">
        <h3>Отправить заявку</h3>
        <div v-if="checked.length !== 0">
          <p>Выбраны услуги:</p>
          <span v-for="item in checked">{{item}}, </span>
        </div>
        <div class="form-inputs">
          <vs-input @change="buttonAble"  v-model="name" placeholder="Имя пользователя">
            <template #icon>
              <i style="color: grey" class='el-icon-user'></i>
            </template>
            <template v-if="name === ''" #message-danger>
              Необходимый
            </template>
          </vs-input>
          <vs-input @change="buttonAble"  type="number" v-model="phone" placeholder="Номер телефона">
            <template #icon>
              <i style="color: grey" class='el-icon-phone'></i>
            </template>
            <template v-if="phone === ''" #message-danger>
              Необходимый
            </template>
          </vs-input>
          <vs-input @change="buttonAble"  v-model="address" placeholder="Ваш адрес">
            <template #icon>
              <i style="color: grey" class='el-icon-location-outline'></i>
            </template>
            <template v-if="address === ''" #message-danger>
              Необходимый
            </template>
          </vs-input>
          <vs-input @change="buttonAble"  type="date" v-model="date">
            <template #icon>
              <i style="color: grey" class='el-icon-date'></i>
            </template>
            <template v-if="date === ''" #message-danger>
              Необходимый
            </template>
          </vs-input>
          <vs-input @change="buttonAble"  type="time" v-model="time">
            <template #icon>
              <i style="color: grey" class='el-icon-timer'></i>
            </template>
            <template v-if="time === ''" #message-danger>
              Необходимый
            </template>
          </vs-input>
          <vs-input @change="buttonAble"  v-model="medicalCard" placeholder="Медицинская карта">
            <template #icon>
