            </template>
            <template v-if="medicalCard === ''" #message-danger>
              Необходимый
            </template>
          </vs-input>
          <vs-button
              :disabled="buttonDisabled"
              @click.prevent="postAppointment"
              style="width: 150px; margin-left: auto; margin-top: 10px" color="#005C5F">
            Записаться
          </vs-button>
        </div>
      </form>
    </div>
    <div class="location_map">
      <iframe
          width="100%"
