        <p style="color: white">медицинский комплекс</p>
      </div>
    </div>
    <div class="footer-content">
      <a :href="'tel:'+clinics.phone_num"><i class="el-icon-mobile-phone"></i>{{ clinics.phone_num }}</a>
      <p><i class="el-icon-location-outline"></i>{{clinics.district}}, {{clinics.street}} {{clinics.home_num}}</p>
    </div>
  </footer>
