<script>
import axios from "axios";
import {mapGetters, mapMutations} from "vuex";
import AboutNavBar from '../components/AboutNavBar'

export default {
  components: { AboutNavBar },
  data() {
    return {
      activeNames: ['0'],
      specialists: [],
      services: [],
      checked: [],
      name: '',
      date: '',
      phone: '',
      time: '',
      address: '',
      medicalCard: '',
      buttonDisabled: true,
    }
  },
  computed: {
    ...mapGetters(['clinics', 'burgerActiveForNav']),
  },
  methods: {
    buttonAble() {
      this.buttonDisabled = !(this.checked.length !== 0 && this.name !== '' && this.date !== '' && this.phone !== '' && this.time !== '' && this.address !== '' && this.address !== '');
    },
    ...mapMutations(['getClinics',]),
    postAppointment() {
      if ( this.checked.length !== 0 && this.name !== '' && this.date !== '' && this.phone !== '' && this.time !== '' && this.address !== '' && this.address !== '' ) {
        axios.post(`http://18.206.211.243/appointments/`,{
          service: this.checked,
          client: {
            full_name: this.name,
            medical_card: this.medicalCard,
            contact: this.phone,
            address: this.address
          },
          date_time: `${this.date}T${this.time}`
        }).then(res => {
          this.$message({
            message: 'Ваше назначение получено, спасибо!',
            type: 'success'
          });
          this.checked = ''
          this.name = ''
          this.date = ''
          this.phone = ''
          this.time = ''
          this.address = ''
          this.medicalCard = ''
          this.buttonAble()
        })
        .catch(error => {
          this.$message({
            message: 'Ваше назначение не получено',
            type: 'error'
          });
        })
      } else {
        if (this.checked.length !== 0) {
          this.$message({
            message: 'Пожалуйста, заполните сервис',
            type: 'error'
          });
        }
        this.$message({
          message: 'Пожалуйста, заполните форму',
          type: 'error'
        });
      }
    },
    changeRouter(id) {
      this.$router.push('/specialist/' + id)
    },
    open(desc) {
      this.$confirm(`${desc}`, 'Описание об услуге', {
      }).then(() => {

      }).catch(() => {

      });
    },
  },
  mounted() {

    setInterval( () => {
      let date = new Date();
      this.hours = date.getHours() < 10 ? '0' + date.getHours() : date.getHours()
      this.minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
      this.seconds = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
      this.dateNow = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
      this.monthNow = date.getMonth() < 10 ? '0' + date.getMonth() : date.getMonth()
      this.day = date.getDay()
      this.yearNow = date.getFullYear()
    }, 1000)
    axios.get(`http://18.206.211.243/clinics/${this.$route.params.id}/specialists`)
        .then(res => {
          this.specialists = res.data
        })
    axios.get(`http://18.206.211.243/clinics/${this.$route.params.id}/services`)
        .then(res => {
          this.services = res.data
        })
    this.getClinics(this.$route.params.id)
  }
}
</script>

